import Hero from '../../Feature/Components/Hero';
import Sawedimage from '../../Assets/sawed-products-1.jpg';
import SawedProductimage from '../../Assets/sawed-products-2.jpg';
import ImageAndText from '../../Feature/Components/ImageAndText';
import { Text } from '../../Shared/value';
import SmallList from '../../Feature/Components/SmallList';

function SawedProducts() {
  return (
    <>
      <Hero
        src={Sawedimage}
        alt="Hero image sgade produkter."
        title="Sgade produkter."
        description="Vi erbjuder svenskt lvtr i sgat format  i flera trslag och anpassade dimensioner."
      />
      <ImageAndText src={SawedProductimage} title="Okantat Mbelvirke">
        <Text>Vi lagerfr bde okantat och kantat virke i flera svenska trslag.</Text>
        <Text>Standardtjocklekar (mm): 19, 25, 32, 50 och 75  beroende p trslag.</Text>
        <Text>Standardlngd r 3 meter, men ek finns i lngder frn 1,8 till 4,3 meter.</Text>
        <Text>Virket kan torkas ner till 7 % fukthalt  s kallat mbeltorrt.</Text>
      </ImageAndText>
      <ImageAndText src={SawedProductimage} title="mnestillverkning" textFirst>
        <Text>Vi kan leverera torkade, hyvlade och lngdkapade mnen i svenskt lvtr.</Text>
        <Text>mnen kan ven snderdelas i lamellsg till exakt nskade mtt.</Text>
        <Text>Vi tillhandahller ocks strips fr vidarefrdling.</Text>
      </ImageAndText>
      <SmallList />
    </>
  );
}

export default SawedProducts;
